@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  /*10px/16px * 100 = 62.5*/ }

body {
  box-sizing: border-box;
  background-color: #222222;
  color: #f0f0f0; }

body {
  font-family: "Merriweather"; }

@-webkit-keyframes animateGrid {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes animateGrid {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes animateHeroimage {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes animateHeroimage {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes animateMovieinfo {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes animateMovieinfo {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.rmdb-grid__header {
  font-family: 'Abel', sans-serif;
  font-size: 3.6rem;
  margin: 3rem 0;
  text-align: center;
  color: #eeeeee;
  letter-spacing: 3px;
  text-transform: uppercase; }
  @media screen and (max-width: 768px) {
    .rmdb-grid__header {
      font-size: 22px; } }

.rmdb-grid__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
  position: relative;
  margin: 3rem 0;
  margin: 0 auto; }
  @media screen and (max-width: 768px) {
    .rmdb-grid__content {
      grid-template-columns: repeat(2, 1fr); } }
  @media screen and (max-width: 375px) {
    .rmdb-grid__content {
      grid-template-columns: repeat(1, 1fr); } }

.rmdb-grid__element {
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s; }

